/* NavBar */
.navbar-custom {
  background-color: #cb5647;
}
.has-search .form-control {
  padding-left: 2.375rem;
}
.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}
.icon-white {
color: #ffffff;
}
/* App Form */
.container {
  max-width: 480px;
  /* margin: 0 auto; */
  margin-top: 75px;
  padding: 0 15px;
}
input[type="text"] {
  width: 100%;
  height: 50px;
  font: 15px/1.4 "Poppins", sans-serif;
  padding: 15px;
  background: #f3f3f3;
  color: #333;
  border: 1px solid transparent;
  border-radius: 10px;
  -webkit-transition: border 0.3s linear;
  transition: border 0.3s linear;
}
input[type="text"]:focus {
  outline: none;
  border: 1px solid #cb5647;
}
button {
  cursor: pointer;
  font: 15px/1.4 "Poppins", sans-serif;
  color: #555;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
button:focus {
  outline: none;
}
h1 {
  font-size: 22px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
/* Task */
.task {
  background: #fff;
  border-radius: 25px;
  padding: 30px;
  -webkit-box-shadow: 0px 0px 40px 0px rgba(0,0,0,0.1);
          box-shadow: 0px 0px 40px 0px rgba(0,0,0,0.1);
}
.title {
  text-align: center;
  margin: 0 0 20px;
}
.form {
  position: relative;
  margin: 0 0 30px;
}
.form button {
  background: none;
  border: none;
  color: #cb5647;
  font-size: 18px;
  position: absolute;
  top: 50%;
  right: 20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
